.iframe-container {
    overflow: hidden;
    padding-top: 56.25%;
    position: relative;
}

.iframe-container iframe {
    border: 5px rgb(245, 194, 194) solid;
    border-radius: 5px;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 80%;
    margin-left: 10%;
  }

.main {
    background-color:  #F4EFEF;
    padding: 10px 10px;
    height: 100%;
    width: 100%;
}
